<template>
  <div class="color-choice-page">
    <div class="color-choice-box">
      <div class="color-choice-title"></div>
      <div class="color-choice-content-layout">
        <div class="color-choice-content">
          <!-- 复制成功提示弹窗 -->
          <div class="success-prompt" :class="{ show: copySuccessText }">
            <p class="color-ele" :style="{ background: copySuccessVal }"></p>
            <div class="color-text">
              <p>复制成功</p>
              <p>{{ copySuccessText }}</p>
            </div>
          </div>
          <div class="btn-grad" @click="copyColorVal('gradualChange')">
            <p>
              有无数种颜色组合可用于创建视觉上吸引人的设计。然而，并不是所有的颜色组合都是一样的。一些颜色组合可以营造和谐感，而另一些则可以营造紧张感。在为您的设计选择颜色时，重要的是要考虑您想要营造的氛围。
              您可以在下面找到两种看起来不错的颜色组合。将它们用于您的下一个网络/设计项目。
            </p>
          </div>
          <!-- 每行盒子 -->
          <div
            class="color-choice-item-box"
            v-for="(item, index) in colorType"
            :key="index"
          >
            <!-- 每个色彩搭配 -->
            <div class="color-choice-item">
              <div
                class="col-1"
                :style="{ background: item.col1 }"
                @click="copyColorVal(item.col1)"
              >
                <h2 :style="{ color: item.col2 }">色彩搭配 {{ index + 1 }}</h2>
                <p :style="{ color: item.col2 }">点击色块复制</p>
                <p :style="{ color: item.col2 }">
                  SubTop网站地址：https://subtoph.github.io/#/home
                </p>
              </div>

              <div
                class="col-2"
                :style="{ background: item.col2 }"
                @click="copyColorVal(item.col2)"
              >
                <p :style="{ color: item.col1 }">
                  SubTop网站地址：https://subtoph.github.io/#/home
                </p>
              </div>
            </div>
            <div class="color-choice-item">
              <div
                class="col-1"
                :style="{ background: item.col2 }"
                @click="copyColorVal(item.col2)"
              >
                <h2 :style="{ color: item.col1 }">色彩搭配 {{ index + 1 }}</h2>
                <p :style="{ color: item.col1 }">点击色块复制</p>
                <p :style="{ color: item.col1 }">
                  SubTop网站地址：https://subtoph.github.io/#/home
                </p>
              </div>

              <div
                class="col-2"
                :style="{ background: item.col1 }"
                @click="copyColorVal(item.col1)"
              >
                <p :style="{ color: item.col2 }">
                  SubTop网站地址：https://subtoph.github.io/#/home
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { color_2, gradualChangeCode } from './colorChoice.js';
export default {
  name: '',
  setup() {
    const data = reactive({
      colorType: color_2, //双色搭配数据
      copySuccessVal: '', //复制成功色值
      copySuccessText: '',
      times: null
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    // 复制网站地址
    const copyColorVal = (colVal) => {
      let myColVal = ''; //保存复制值,颜色值和渐变代码
      let input = null; //保存临时创建的元素
      if (colVal === 'gradualChange') {
        //复制渐变代码创建textarea
        input = document.createElement('textarea');
        myColVal = gradualChangeCode;
      } else {
        // 复制颜色值创建input
        input = document.createElement('input');
        myColVal = colVal;
      }

      clearTimeout(data.times);
      // 添加到body中
      document.body.appendChild(input);
      // 设置input的值
      input.value = myColVal;
      // input.setAttribute('value', myColVal);
      // 选择文本域内容
      input.select();
      if (document.execCommand('copy')) {
        document.execCommand('copy');
      }
      // 显示复制成功提示,背景渐变提示,色值提示
      if (colVal === 'gradualChange') {
        data.copySuccessVal = '#77a1d3';
        data.copySuccessText = '背景渐变';
      } else {
        data.copySuccessVal = myColVal;
        data.copySuccessText = myColVal;
      }

      // 延迟自动清除提示
      data.times = setTimeout(() => {
        data.copySuccessVal = '';
        data.copySuccessText = '';
      }, 2000);
      // 删除临时input元素
      document.body.removeChild(input);
    };
    return {
      copyColorVal,
      ...toRefs(data)
    };
  }
};
</script>
